import React from "react";
import BeaconHeader from "../../commonComponents/BeaconHeader/BeaconHeader";
import "./HomeLogin.css";
import { BASE_URL, DTX_GRAPHQL_URL } from "../../../config";
import { DTX_BASE_URL } from "../../../config";
import {
  setBomMappingData,
  setMMmappingData,
  setNextPage,
  setTenantUserId,
} from "../../redux-toolkit/slice/TaskpaneSlice";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { g } from "../../../globalState/globalState";
import Button from "../../commonComponents/Button/Button";
import { setBomMappingTempData2, setMMmappingTempData2, setUploadLoader } from "../../redux-toolkit/slice/action";
import Loading from "../../commonComponents/Loading/Loading";
import { runQuery } from "../../api/apiCalls";
import { GetBomMaterialMappingConfigQuery } from "../../api/graphQlQuery";
import { logout } from "../../constFunctions/copmponentFunctions";
import { JWT_ERROR_MESSAGE } from "../../constFunctions/constants";
function HomeLogin() {
  const dispatch = useDispatch();
  const { uploadLoader } = useSelector((state) => state.action);

  // handle login initailly call another html page (dialogue box) , if the login successfull then will get a response(loginToken) from dialogue box
  // the response(token ) will save as beaconToken in loaclStorage and closes the dialogue box

  const handleLogin = () => {
    g.dialog;
    try {
      Office.context.ui.displayDialogAsync(
        `${BASE_URL}/login.html`,
        { height: 62, width: 45, displayInIframe: true },
        function (asyncResult) {
          g.dialog = asyncResult.value;
          g.dialog.addEventHandler(Office.EventType.DialogMessageReceived, processMessage);
        }
      );
      const processMessage = (arg) => {
        localStorage.setItem("beaconToken", arg.message);
        dispatch(setUploadLoader(true));
        g.dialog.close();
        getTenantToken();
        dispatch(setNextPage("material"));
      };
    } catch (error) {
      console.log(error);
      dispatch(setUploadLoader(false));
    }
  };
  // to fetch tenant token
  function getTenantToken() {
    try {
      let token = localStorage.getItem("beaconToken");

      let decodedJwt = parseJwt(token);
      localStorage.setItem("tenantId", decodedJwt["x-hasura-tenant-id"]);
      localStorage.setItem("expiry", decodedJwt["exp"]);
      dispatch(setTenantUserId(decodedJwt["x-hasura-user-id"]));
      const headers = {
        "Content-Type": "application/json",
        Authorization: "Bearer " + token,
      };
      axios
        .post(
          `${DTX_BASE_URL}/user/tokenExchange`,
          // eslint-disable-next-line no-undef, prettier/prettier
          { tenantId: decodedJwt["x-hasura-tenant-id"] },
          {
            headers: headers,
          }
        )
        .then((response) => {
          localStorage.setItem("tenantToken", response?.data?.message);
          localStorage.setItem("projectExchangeToken", response?.data?.message);
          getLatestMappingData();
          dispatch(setUploadLoader(false));
        })
        .catch((error) => {
          console.log(error);
          dispatch(setUploadLoader(false));
        });
    } catch (error) {
      console.log("Error:", error);
    } finally {
      dispatch(setUploadLoader(false));
    }
  }
  // parseJwt is a function to decode given token as argument and returns decoded vlaues
  function parseJwt(token) {
    try {
      var base64Url = token.split(".")[1];
      var base64 = base64Url.replace(/-/g, "+").replace(/_/g, "/");
      var jsonPayload = decodeURIComponent(
        window
          .atob(base64)
          .split("")
          .map(function (c) {
            return "%" + ("00" + c.charCodeAt(0).toString(16)).slice(-2);
          })
          .join("")
      );

      return JSON.parse(jsonPayload);
    } catch (error) {
      console.log(error);
    }
  }
  // function which fetches all material and bom mapping data;
  const getLatestMappingData = () => {
    try {
      const body = {
        operationName: "GetBomMaterialMappingConfigQuery",
        query: GetBomMaterialMappingConfigQuery,
        variables: { tenant_id: localStorage.getItem("tenantId") },
      };
      runQuery(DTX_GRAPHQL_URL, localStorage.getItem("tenantToken"), body)
        .then((data) => {
          if (data.errors) {
            data.errors.map((element) => {
              if (element?.message.includes(JWT_ERROR_MESSAGE)) {
                logout();
                dispatch(setNextPage("homeLogin"));
              }
            });
          } else {
            dispatch(setMMmappingData(data?.data?.tenant_defaults[0].material));
            dispatch(setBomMappingData(data?.data?.tenant_defaults[0].bom));
            dispatch(setMMmappingTempData2(data?.data?.tenant_defaults[0].material));
            dispatch(setBomMappingTempData2(data?.data?.tenant_defaults[0].bom));
          }
        })
        .catch((error) => {
          console.log(error.message, "error.message");
        });
    } catch (error) {
      console.log(error);
    } finally {
    }
  };
  return (
    <>
      <div className="HomeLoginBody " id="commonContainer">
        <BeaconHeader />
        <main id="app-body" className="loginHome">
          <span onClick={handleLogin}>
            <Button label={"Login"} />
          </span>
        </main>
      </div>
      {uploadLoader && <Loading />}
    </>
  );
}

export default HomeLogin;
